// Fix the Bootstrap font path so it points to wwwroot/fonts instead of wwwroot/fonts/bootstrap.
$icon-font-path: "../fonts/";

/**
/* ---------------------------------------------- /*
 * General styles
/* ---------------------------------------------- */

body {
    font: 400 15px/1.6 'serif', sans-serif;
    color: #545454;
}

img {
    max-width: 100%;
    height: auto;
}

a {
    color: #EF4836;
}

a:hover {
    color: #C4C4C4;
}

.input-group .validationMessage {
    display: table-caption;
    caption-side: bottom;
}

.validationMessage {
    color: Red;
}

textarea {
    -moz-resize: vertical;
    -ms-resize: vertical;
    -o-resize: vertical;
    resize: vertical;
}

/* ---------------------------------------------- /*
 * Typography
/* ---------------------------------------------- */

h1, h2, h3, h4, h5, h6 {
    font: 600 46px/1.2 'serif', sans-serif;
    margin: 5px 0 5px;
    color: #222;
}

h1 {
    font-size: 64px;
    font-weight: 700;
    margin: 5px 0 15px;
}

h2 {
    font-size: 40px;
    font-weight: 700;
}

h3 {
    font-size: 22px;
    text-transform: uppercase;
    margin: 5px 0 15px;
}

h4 {
    font-size: 16px;
    margin: 5px 0 10px;
}

h5 {
    font-size: 14px;
}

/* ---------------------------------------------- /*
 * Transition elsements
/* ---------------------------------------------- */

a,
.btn,
.navbar,
.form-control {
    -webkit-transition: all 0.125s ease-in-out 0;
    -moz-transition: all 0.125s ease-in-out 0;
    -ms-transition: all 0.125s ease-in-out 0;
    -o-transition: all 0.125s ease-in-out 0;
    transition: all 0.125s ease-in-out 0;
}

/* ---------------------------------------------- /*
 * Reset box-shadow
/* ---------------------------------------------- */

.btn,
.panel,
.form-control,
.modal-content,
.form-control:hover,
.form-control:focus {
    -webkit-box-shadow: none;
    -ms-box-shadow: none;
    box-shadow: none;
}

/* ---------------------------------------------- /*
 * Reset border-radius
/* ---------------------------------------------- */

.btn,
.label,
.avatar,
.form-control,
.modal-content,
.panel-group .panel,
.widget-social li a {
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    -ms-border-radius: 3px;
    border-radius: 3px;
}

/* ---------------------------------------------- /*
 * Custom button style
/* ---------------------------------------------- */

.btn {
    font-weight: 700;
    font-size: 14px;
    padding: 17px 27px;
    border: 0;
}

.btn:hover, .btn:focus, .btn:active, .btn.active {
    outline: inherit !important;
}

/* Button icons */

.btn .icon-before {
    margin-right: 6px;
}

.btn .icon-after {
    margin-left: 6px;
}

/* Button types*/

.btn-default {
    background: #EEEEEE;
}

.btn-custom-1, .btn-custom-2 {
    background: transparent;
    border: 2px solid #468cf5;
    padding: 15px 25px;
}

.btn-custom-2 {
    border: 2px solid #222;
}

.btn-custom-1:hover, .btn-custom-1:focus, .btn-custom-1:active, .btn-custom-1.active {
    background: #468cf5;
    border: 2px solid #468cf5;
    color: #FFF;
}

.btn-custom-2:hover, .btn-custom-2:focus, .btn-custom-2:active, .btn-custom-2.active {
    background: #222;
    border: 2px solid #222;
    color: #FFF;
}

/* ---------------------------------------------- /*
 * Skill-bars
/* ---------------------------------------------- */

.skill-bar {
    background: #C4C4C4;
    display: table;
    height: 4px;
    width: 100%;
    margin: 10px -2px 20px -2px;
}

.skill-bar .skill-rate-on {
    background: #353535;
}

.skill-rate-on, .skill-rate-off {
    display: table-cell;
    border-left: 2px solid #FFF;
    border-right: 2px solid #FFF;
}

/* ---------------------------------------------- /*
 * Modal
/* ---------------------------------------------- */

.modal-content {
    border: 0;
}

/* ---------------------------------------------- /*
 * General
/* ---------------------------------------------- */

.form-control {
    background: #FFF;
    border: 2px solid #C4C4C4;
    height: 54px;
    font-size: 14px;
    padding: 15px;
    color: #777;
}

.wrapper {
    overflow: hidden;
}

.section, .callout {
    position: relative;
    padding: 120px 0;
}

.callout {
    background: #F5F5F5;
}

.headline {
    margin: 0 0 60px;
}

.headline p {
    font-weight: 700;
    color: #C4C4C4;
}

.remove-shadow {
    -ms-text-shadow: 0 0 0 !important;
    text-shadow: 0 0 0 !important;
}

.icon-list {
    list-style: none;
    padding: 0;
}

.icon-list > li > i {
    margin-right: 6px;
}

h3 > .fa {
    font-size: 24px;
    margin-right: 6px;
}

.mouse-icon {
    position: relative;
    border: 2px solid #fff;
    -ms-border-radius: 16px;
    border-radius: 16px;
    height: 50px;
    width: 30px;
    margin: 0 auto;
    display: block;
    z-index: 10;
}

.mouse-icon .wheel {
    -webkit-animation-name: drop;
    -webkit-animation-duration: 1s;
    -webkit-animation-timing-function: linear;
    -webkit-animation-delay: 0;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-play-state: running;
    -moz-animation-name: drop;
    -ms-animation-name: drop;
    -o-animation-name: drop;
    animation-name: drop;
    -moz-animation-duration: 1s;
    -ms-animation-duration: 1s;
    -o-animation-duration: 1s;
    animation-duration: 1s;
    -moz-animation-timing-function: linear;
    -ms-animation-timing-function: linear;
    -o-animation-timing-function: linear;
    animation-timing-function: linear;
    -moz-animation-delay: 0;
    -ms-animation-delay: 0;
    -o-animation-delay: 0;
    animation-delay: 0;
    -moz-animation-iteration-count: infinite;
    -ms-animation-iteration-count: infinite;
    -o-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -moz-animation-play-state: running;
    -ms-animation-play-state: running;
    -o-animation-play-state: running;
    animation-play-state: running;
    position: relative;
    -ms-border-radius: 10px;
    border-radius: 10px;
    background: #fff;
    width: 4px;
    height: 10px;
    top: 4px;
    margin-left: auto;
    margin-right: auto;
}

@-webkit-keyframes drop {
    0% {
        top: 5px;
        opacity: 0;
    }

    30% {
        top: 10px;
        opacity: 1;
    }

    100% {
        top: 25px;
        opacity: 0;
    }
}

@-moz-keyframes drop {
    0% {
        top: 5px;
        opacity: 0;
    }

    30% {
        top: 10px;
        opacity: 1;
    }

    100% {
        top: 25px;
        opacity: 0;
    }
}

@keyframes drop {
    0% {
        top: 5px;
        opacity: 0;
    }

    30% {
        top: 10px;
        opacity: 1;
    }

    100% {
        top: 25px;
        opacity: 0;
    }
}

.no-padding {
    padding: 0 !important;
}

/* ---------------------------------------------- /*
 * Navigation
/* ---------------------------------------------- */

.navbar {
    background: none;
    -ms-border-radius: 0;
    border-radius: 0;
    border: 0;
    padding: 20px 0;
    text-transform: uppercase;
    color: #FFF !important;
}

.navbar-custom .nav li a:hover,
.navbar-custom .nav li a:focus,
.navbar-custom .nav li.active {
    background: none;
    outline: 0;
    color: #EF4836;
}

.navbar-custom .navbar-brand {
    font: 700 18px/1.2 'serif', sans-serif;
    color: #FFF;
}

.navbar-custom .navbar-nav > li.active {
    border-bottom: 3px solid #FFF;
}

.navbar-custom .navbar-toggle .icon-bar {
    background: #FFF;
}

.navbar-custom .navbar-nav > li > a,
.navbar-custom .navbar-nav .dropdown-menu > li > a {
    color: #FFF;
    font-size: 12px;
}

/* uncss:ignore */
.navbar-color,
/* uncss:ignore */
.custom-collapse {
    background: #FFF;
    -webkit-box-shadow: 0 0 4px #cbcbb2;
    -ms-box-shadow: 0 0 4px #cbcbb2;
    box-shadow: 0 0 4px #cbcbb2;
    padding: 0;
}
/* uncss:ignore */
.navbar-color .navbar-brand,
/* uncss:ignore */
.custom-collapse .navbar-brand,
/* uncss:ignore */
.navbar-color .navbar-nav > li > a,
/* uncss:ignore */
.navbar-color .navbar-nav .dropdown-menu > li > a,
/* uncss:ignore */
.custom-collapse .navbar-nav > li > a,
/* uncss:ignore */
.custom-collapse .navbar-nav .dropdown-menu > li > a {
    color: #222;
}
/* uncss:ignore */
.navbar-color .navbar-nav > li.active {
    border-bottom: 3px solid #222;
}
/* uncss:ignore */
.custom-collapse .navbar-nav {
    text-align: left;
}
/* uncss:ignore */
.custom-collapse .navbar-nav > li.active {
    border: 0;
}
/* uncss:ignore */
.navbar-color .navbar-toggle .icon-bar,
/* uncss:ignore */
.custom-collapse .navbar-toggle .icon-bar {
    background: #222;
}

/* ---------------------------------------------- /*
 * Home
/* ---------------------------------------------- */

#home {
    padding: 260px 0 160px 0;
    text-align: center;
    color: #FFF;
}

#home h1, #home h2, #home h3, #home h4, #home h5, #home h6 {
    color: #FFF;
}

.hello {
    margin: 0 0 100px;
}

/* ---------------------------------------------- /*
 * Profile-contact
/* ---------------------------------------------- */

#profile-contact {
    -webkit-box-shadow: 0 0 4px #cbcbb2;
    -ms-box-shadow: 0 0 4px #cbcbb2;
    box-shadow: 0 0 4px #cbcbb2;
    padding: 20px 0;
}

.profile-item i {
    display: block;
    line-height: 42px;
    font-size: 32px;
    color: #222;
}

.profile-widget {
    margin-bottom: 10px;
}

.widget-social {
    padding: 0;
    margin: 0;
    width: 100%;
    text-align: center; /* this will center the list items */
}

.widget-social li {
    display: inline-block;
    margin-right: 5px;
}

.widget-social li a {
    background: #2e2e2e;
    text-align: center;
    display: block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    font-size: 1.25em;
    color: #FFF;
}

.widget-social li a:hover {
    background: #EF4836;
}

.stat .stat-icon {
    margin-bottom: 10px;
    font-size: 32px;
    color: #222;
}

.stat .stat-icon i {
    margin-right: 10px;
}


/* ---------------------------------------------- /*
 * Events
/* ---------------------------------------------- */

.events-per-year {
    margin-bottom: 40px;
}

.event-place {
    margin-bottom: 20px;
}

.event-place > i,
.event-place > h4 > i {
    color: #C4C4C4;
    font-size: 14px;
    margin-right: 5px;
}

.collateral {
    list-style-type: none;
    padding-left: 0;
}

@media only screen and (min-width : 800px) {
    .collateral li {
        display: inline-block;
    }

    .collateral li:after {
        content: ' |';
    }

    nav li:last-child:after {
        content: '';
    }

    .collateral li:before {
        content: '\00a0';
    }

    .collateral li:first-child:before {
        content: '[ ';
    }

    .collateral li:last-child:after {
        content: ' ]';
    }
}
/* ---------------------------------------------- /*
 * Footer
/* ---------------------------------------------- */

#footer {
    background: #141414;
    padding: 5px 0;
    color: #FFF;
}

#footer .copy {
    font-size: 10px;
    margin: 0 20px 0 20px;
}

/* ---------------------------------------------- /*
 * Loader
/* ---------------------------------------------- */
#loader {
    width: 100%;
    height: 100%;
    position: fixed;
    background: #FFF;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: 9999;
}

#status, .status-mes {
    background-image: url('../images/loader.gif');
    background-position: center;
    background-repeat: no-repeat;
    -ms-background-size: 70px 70px;
    background-size: 70px 70px;
    width: 200px;
    height: 200px;
    left: 50%;
    top: 50%;
    margin: -100px 0 0 -100px;
    position: absolute;
}

.status-mes {
    background: none;
    left: 0;
    margin: 0;
    text-align: center;
    top: 65%;
}

.status-mes h4 {
    color: #222;
    margin-top: 30px;
}

/* ---------------------------------------------- /*
 * Error page
/* ---------------------------------------------- */
.errorPage {
    padding: 0;
    background: #fefefe;
    text-align: center;
    color: #eee;
}

.error-image {
    max-height: 400px;
    width: 20%;
    margin-top: 30px;
}
/* ---------------------------------------------- /*
 * Media
/* ---------------------------------------------- */

@media (max-width: 767px) {
    .wrapper {
        text-align: center;
    }

    #profile-contact .profile-item {
        margin: 0 0 20px;
    }

    #stats .stat {
        margin: 0 0 20px;
    }
}
